<template>
  <div>
    <NavTemplent title="运营部门指标项"></NavTemplent>
    <div style="padding: 15px 10% 15px 15px;">
      <div>
        <h5 style="font-weight: bold">数据筛选</h5>
        <el-form style="margin-top: 15px" label-width="80px" inline @submit.native.prevent size="mini">
          <el-date-picker
            style="width: 250px"
            size="mini"
            v-model="form.date"
            type="daterange"
            format="yyyy/MM/dd"
            start-placeholder="开始日期"
            end-placeholder="结束日期">
          </el-date-picker>
          <el-form-item label="姓名" v-if="!isDepartent">
            <el-input v-model="form.userName" placeholder="请输入姓名"></el-input>
          </el-form-item>
          <el-form-item style="text-align: right">
            <el-button type="primary" @click="getUserList">
              <i class="el-icon-search"></i>
              筛选</el-button>
          </el-form-item>
        </el-form>
      </div>
      <div v-if="!isDepartent">
        <div class="tableTitle">
        <span>
          <div>指标列表</div>
        </span>
        </div>
        <el-table
          :data="userList"
          border
          style="width: 100%">
          <el-table-column
            type="index">
          </el-table-column>
          <el-table-column
            label="员工姓名">
            <template slot-scope="scope">
              <el-link @click="openDetails(scope.row)" style="color:#409EFF">{{scope.row.name || '--'}}</el-link>
            </template>
          </el-table-column>
          <el-table-column
            label="KPI">
            <template slot-scope="scope">
              <span>{{scope.row.department_name || '--'}}</span>
            </template>
          </el-table-column>
          <el-table-column
            label="目标值">
            <template slot-scope="scope">
              <span>{{scope.row.role_name || '--'}}</span>
            </template>
          </el-table-column>
          <el-table-column
            label="实际完成值">
            <template slot-scope="scope">
              <el-switch
                @change="updateStatus(scope.row)"
                v-model="!!scope.row.status"
                active-color="#13ce66"
                inactive-color="#ff4949">
              </el-switch>
            </template>
          </el-table-column>
          <el-table-column
            label="该项得分">
            <template slot-scope="scope">
              <span>{{ scope.row.created_at | dateFormat('YYYY/MM/DD')}}</span>
            </template>
          </el-table-column>
          <el-table-column
            label="备注">
            <template slot-scope="scope">
              <span>{{ scope.row.created_at | dateFormat('YYYY/MM/DD')}}</span>
            </template>
          </el-table-column>
          <el-table-column
            label="时间">
            <template slot-scope="scope">
              <span>{{ scope.row.created_at | dateFormat('YYYY/MM/DD')}}</span>
            </template>
          </el-table-column>
          <el-table-column
            label="操作">
            <template slot-scope="scope">
              <el-link @click="editUseMessage(scope.row)" style="color:#409EFF">编辑</el-link>
              <el-link @click="delUseMessage(scope.row)" style="color:#409EFF">删除</el-link>
            </template>
          </el-table-column>
        </el-table>
        <TablePage :pageSize="page.pageSize" :pageNum="page.pageNum" :total="total"></TablePage>
      </div>
      <div v-if="isDepartent">
        <div class="tableTitle">
        <span style="width: 50%;float: left">
          <div>数据列表</div>
        </span>
          <span style="width: 50%;float: right;text-align: right">
          <el-button size="mini" type="primary" @click="editUseMessage"><i class="el-icon-plus"></i>添加</el-button>
        </span>
        </div>
        <el-table
          :data="userList"
          border
          style="width: 100%">
          <el-table-column
            type="index">
          </el-table-column>
          <el-table-column
            label="指标名称">
            <template slot-scope="scope">
              <el-link style="color:#409EFF">{{scope.row.name || '--'}}</el-link>
            </template>
          </el-table-column>
          <el-table-column
            label="目标值">
            <template slot-scope="scope">
              <span>{{scope.row.department_name || '--'}}</span>
            </template>
          </el-table-column>
          <el-table-column
            label="实际完成值">
            <template slot-scope="scope">
              <span>{{scope.row.role_name || '--'}}</span>
            </template>
          </el-table-column>
          <el-table-column
            label="得分项">
            <template slot-scope="scope">
              <span>{{ scope.row.created_at | dateFormat('YYYY/MM/DD')}}</span>
            </template>
          </el-table-column>
          <el-table-column
            label="时间">
            <template slot-scope="scope">
              <span>{{ scope.row.created_at | dateFormat('YYYY/MM/DD')}}</span>
            </template>
          </el-table-column>
          <el-table-column
            label="操作">
            <template slot-scope="scope">
              <el-link @click="editUseMessage(scope.row)" style="color:#409EFF">编辑</el-link>
              <el-link @click="delUseMessage(scope.row)" style="color:#409EFF">删除</el-link>
            </template>
          </el-table-column>
        </el-table>
      </div>

      <el-dialog
        :title="title"
        :visible.sync="dialogVisible"
        width="750px" append-to-body>
        <el-form size="mini">
          <el-form-item label="姓名" v-if="!uditUser">
            <el-input v-model="form.name" placeholder="请输入部门负责人名称"></el-input>
          </el-form-item>
          <el-form-item label="指标" v-if="!uditUser">
            <el-input v-model="form.mobile" placeholder="请输入手机号"></el-input>
          </el-form-item>
          <el-form-item label="目标值" v-if="!uditUser">
            <el-input type="password" v-model="form.password" placeholder="******"></el-input>
          </el-form-item>
          <el-form-item label="单位" v-if="!uditUser">
            <el-input v-model="form.email" placeholder="请输入邮箱"></el-input>
          </el-form-item>
          <el-form-item label="目标期类型">
            <el-select v-model="form.department_id" placeholder="请选择部门">
              <el-option v-for="item in departmentList" :key="item.department_id" :value="item.department_id" :label="item.department_name"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="实际完成值" v-if="uditUser" >
            <el-select v-model="form.role_id" placeholder="请选择角色" multiple>
              <el-option v-for="item in roleList" :key="item.role_id" :label="item.role_name" :value="item.role_id"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="该项得分" v-if="!uditUser">
            <el-input v-model="form.mobile" placeholder="请输入手机号"></el-input>
          </el-form-item>
          <el-form-item label="选择日期" v-if="!uditUser">
            <el-date-picker
              v-model="form.chooseTime"
              type="date"
              placeholder="选择日期">
            </el-date-picker>
          </el-form-item>
          <el-form-item label="备注" v-if="!uditUser">
            <el-input v-model="form.email" placeholder="请输入邮箱"></el-input>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button size="mini" type="danger" @click="cancle">取 消</el-button>
          <el-button size="mini" type="primary" @click="save">确 定</el-button>
        </div>
      </el-dialog>
    </div>
  </div>
</template>
<script>
    import NavTemplent from "../../../components/NavTemplent";
    import TablePage from "../../../components/TablePage";
    export default {
        components: {
            NavTemplent,
            TablePage
        },
        data() {
            return {
                editUserMessage: null,
                departmentList: [],
                page: {
                    pageSize: 1,
                    pageNum: 10
                },
                editUse: null,
                title: '用户信息',
                isDepartent: false,
                dialogVisible: false,
                userList: [],
                roleList: [],
                total: 0,
                updateStatusData: null,
                menu: false,
                uditUser: false,
                form: {
                    chooseTime: '',
                    date: '',
                    departmentId: '',
                    password: '',
                    mobile: '',
                    userName: '',
                    department_id: '',
                    email: '',
                    name: '',
                    role_id: [],
                    status: 0
                }
            }
        },
        methods: {
            getUserList() {
                this.$post('/getUserList', {
                    user_name: this.form.userName,
                    department_id: this.form.departmentId
                }).then(res => {
                    this.total = res.count;
                    this.userList = res.list
                })
            },
            getDepartmentList() {
                this.$post('/getDepartmentList').then(res => {
                    this.departmentList = res.list
                }).catch(err => {
                    this.$message.error(err)
                });
            },
            getRoleList() {
                this.$post('getRoleList').then(res => {
                    this.roleList = res.list.filter(item => {
                        return item.status === 1
                    })
                })
            },
            openDetails() {
                this.isDepartent = true;
            },
            editUseMessage(data) {
                this.editUserMessage = data;
                this.dialogVisible = true;
                if (data.id) {
                    this.form = data;
                    this.menu = true;
                    this.uditUser = false;
                }else {
                    this.form.mobile = '';
                    this.form.email = '';
                    this.form.name = '';
                    this.form.department_id = '';
                    this.form.role_id = [];
                    this.menu = false;
                    this.uditUser = false;
                }
            },
            delUseMessage() {
                alert('删除用户')
            },
            cancle() {
                this.dialogVisible = false;
                this.$emit('close')
            },
            save() {
                if (this.editUserMessage.id) {
                    if (this.form.mobile === this.editUserMessage.mobile) {
                        this.mobile = null
                    } else {
                        this.mobile = this.form.mobile
                    }
                    if (this.uditUser) {
                        this.$post('editUser', {
                            email: this.editUserMessage.email,
                            user_id: this.editUserMessage.id,
                            user_name: this.editUserMessage.name,
                            mobile: null,
                            department_id: this.form.department_id,
                            password: this.editUserMessage.password,
                            role_list: JSON.stringify(this.form.role_id)
                        }).then(res => {
                            this.$post('editUserStatus', {
                                user_id: this.editUserMessage.id
                            }).then(res => {
                                this.$message.success('状态修改成功');
                                this.dialogVisibles = false;
                                this.$emit('close');
                                this.getUserList()
                            });
                        })
                    } else {
                        this.$post('editUser', {
                            email: this.form.email,
                            user_id: this.editUserMessage.id,
                            user_name: this.form.name,
                            mobile: this.mobile,
                            department_id: this.form.department_id,
                            password: this.form.password,
                        }).then(res => {
                            this.$message.success('用户修改成功');
                            this.dialogVisibles = false;
                            this.$emit('close');
                            this.getUserList()
                        })
                    }

                }else {
                    console.log(this.form);
                    this.$post('signUp', this.form).then(res => {
                        this.$message.success('用户新增成功');
                        this.dialogVisibles = false;
                        this.$emit('close');
                        this.getUserList()
                    })
                }
            }
        },
        mounted() {
            this.getUserList();
            this.getDepartmentList();
            this.getRoleList();
        }
    }
</script>

<style lang="scss" scoped>
  .tableTitle{
    height: 60px;
    line-height: 60px;
    padding: 0 15px;
    background: #eee;
    margin-bottom: 15px;
    div{
      font-weight: bolder;
    }
  }
  .el-select{
    width: 100%;
  }
</style>



