<template>
  <div>
    <NavTemplent title="业务部门"></NavTemplent>
    <div style="padding: 15px 10% 15px 15px;">
      <div>
        <h5 style="font-weight: bold">数据筛选</h5>
        <el-form style="margin-top: 15px" label-width="80px" inline @submit.native.prevent size="mini">
          <el-date-picker
            style="width: 250px"
            size="mini"
            v-model="form.date"
            type="daterange"
            format="yyyy/MM/dd"
            start-placeholder="开始日期"
            end-placeholder="结束日期">
          </el-date-picker>
          <el-form-item>
            <el-button type="primary">
              <i class="el-icon-search"></i>
              筛选</el-button>
          </el-form-item>
        </el-form>
      </div>
      <div class="tableTitle">
        <span>
          <div>业务部门</div>
        </span>
      </div>
      <div>
        <div id="myChart" style="width: 100%;height: 300px;padding: 15px"></div>
      </div>
      <div class="tableTitle">
        <span>
          <div>ROI排行榜</div>
        </span>
      </div>
      <el-table
        :data="listData"
        border
        style="width: 100%">
        <el-table-column
          label="排名">
          <template slot-scope="scope">
            <span>{{scope.row.website_name || '--'}}</span>
          </template>
        </el-table-column>
        <el-table-column
          label="姓名">
          <template slot-scope="scope">
            <span>{{scope.row.website_type || '--'}}</span>
          </template>
        </el-table-column>
        <el-table-column
          label="KPI">
          <template slot-scope="scope">
            <span>{{scope.row.update_time * 1000 | dateFormat('YYYY/MM/DD')}}</span>
          </template>
        </el-table-column>
      </el-table>
    </div>
  </div>
</template>
<script>
    import NavTemplent from "../../../components/NavTemplent";
    import TablePage from "../../../components/TablePage";
    export default {
        components: {
            NavTemplent,
            TablePage
        },
        data() {
            return {
                listData: [],
                page: {
                    pageSize: 1,
                    pageNum: 10
                },
                form: {
                    date: [],
                }
            }
        },
        methods: {
            drawLine() {
                let myChart = this.$echarts.init(document.getElementById('myChart'));
                myChart.setOption({
                    title: {
                        text: '折线图堆叠'
                    },
                    tooltip: {
                        trigger: 'axis'
                    },
                    legend: {
                        data:['邮件营销','联盟广告','视频广告','直接访问','搜索引擎']
                    },
                    grid: {
                        left: '3%',
                        right: '4%',
                        bottom: '3%',
                        containLabel: true
                    },
                    toolbox: {
                        feature: {
                            saveAsImage: {}
                        }
                    },
                    xAxis: {
                        type: 'category',
                        boundaryGap: false,
                        data: ['周一','周二','周三','周四','周五','周六','周日']
                    },
                    yAxis: {
                        type: 'value'
                    },
                    series: [
                        {
                            name:'邮件营销',
                            type:'line',
                            stack: '总量',
                            data:[120, 132, 101, 134, 90, 230, 210]
                        },
                        {
                            name:'联盟广告',
                            type:'line',
                            stack: '总量',
                            data:[220, 182, 191, 234, 290, 330, 310]
                        },
                        {
                            name:'视频广告',
                            type:'line',
                            stack: '总量',
                            data:[150, 232, 201, 154, 190, 330, 410]
                        },
                        {
                            name:'直接访问',
                            type:'line',
                            stack: '总量',
                            data:[320, 332, 301, 334, 390, 330, 320]
                        },
                        {
                            name:'搜索引擎',
                            type:'line',
                            stack: '总量',
                            data:[820, 932, 901, 934, 1290, 1330, 1320]
                        }
                    ]
                });
            }
        },
        mounted() {
            this.drawLine()
        }
    }
</script>

<style lang="scss" scoped>
  .tableTitle{
    height: 60px;
    line-height: 60px;
    padding: 0 15px;
    background: #eee;
    margin-bottom: 15px;
    div{
      font-weight: bolder;
    }
  }
  .el-select{
    width: 100%;
  }
  .tab {
    margin: 10px;
    .active{
      background: #409EFF;
      color: #fff;
    }
    span {
      font-size: 14px;
      border-radius: 5px;
      border: 1px solid #e6e3e3;
      padding: 5px 10px;
    }
  }
</style>



