<template>
  <div>
    <NavTemplent title="部门负责人指标库"></NavTemplent>
    <div style="padding: 15px 10% 15px 15px;">
      <div>
        <h5 style="font-weight: bold">数据筛选</h5>
        <el-form style="margin-top: 15px" label-width="80px" inline @submit.native.prevent size="mini">
          <el-date-picker
            size="mini"
            v-model="form.date"
            type="daterange"
            format="yyyy/MM/dd"
            range-separator="至"
            start-placeholder="开始日期"
            end-placeholder="结束日期">
          </el-date-picker>
          <el-form-item label="部门">
            <el-select v-model="form.department_id" placeholder="请选择部门">
              <el-option v-for="item in departmentList" :key="item.department_id" :value="item.department_id" :label="item.department_name"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="周期">
            <el-select v-model="form.cycle" placeholder="请选择站点">
              <el-option :value="0" label="日"></el-option>
              <el-option :value="1" label="月"></el-option>
              <el-option :value="2" label="年"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item>
            <el-button type="primary">
              <i class="el-icon-search"></i>
              筛选</el-button>
          </el-form-item>
        </el-form>
      </div>
      <el-row style="border: 1px solid #e6e3e3">
        <el-col :span="12" style="padding: 0 15px">
          <p style="font-size: 14px;font-weight: bolder;padding: 0 15px">
            <span style="text-align: left">人事部门</span>
            <span style="text-align: right;cursor: pointer" @click="openDetails">查看详情</span>
          </p>
          <el-row>
            <el-col :span="6" style="padding: 5px;text-align: center">
              <p style="font-weight: bolder">月数据</p>
              <el-progress type="circle" :stroke-width="10" :percentage="25"></el-progress>
            </el-col>
            <el-col :span="6" style="padding: 5px;text-align: center">
              <p style="font-weight: bolder">约面数</p>
              <el-progress type="circle" :stroke-width="10" :percentage="25"></el-progress>
            </el-col>
            <el-col :span="6" style="padding: 5px;text-align: center">
              <p style="font-weight: bolder">转化率</p>
              <el-progress type="circle" :stroke-width="10" :percentage="25"></el-progress>
            </el-col>
            <el-col :span="6" style="padding: 5px;text-align: center">
              <p style="font-weight: bolder">录入量</p>
              <el-progress type="circle" :stroke-width="10" :percentage="25"></el-progress>
            </el-col>
          </el-row>
        </el-col>
        <el-col :span="12" style="padding: 0 15px">
          <p style="font-size: 14px;font-weight: bolder;padding: 0 15px">
            <span>数据对比</span>
          </p>
          <el-row>
            <el-col :span="18" style="padding: 5px;text-align: center">
              <div id="myChart" style="width: 100%;height: 200px"></div>
            </el-col>
          </el-row>
        </el-col>
      </el-row>
      <el-row style="border: 1px solid #e6e3e3">
        <el-col :span="12" style="padding: 0 15px">
          <p style="font-size: 14px;font-weight: bolder;padding: 0 15px">
            <span style="text-align: left">人事部门</span>
            <span style="text-align: right;cursor: pointer" @click="openDetails">查看详情</span>
          </p>
          <el-row>
            <el-col :span="6" style="padding: 5px;text-align: center">
              <p style="font-weight: bolder">月数据</p>
              <el-progress type="circle" :stroke-width="10" :percentage="25"></el-progress>
            </el-col>
            <el-col :span="6" style="padding: 5px;text-align: center">
              <p style="font-weight: bolder">约面数</p>
              <el-progress type="circle" :stroke-width="10" :percentage="25"></el-progress>
            </el-col>
            <el-col :span="6" style="padding: 5px;text-align: center">
              <p style="font-weight: bolder">转化率</p>
              <el-progress type="circle" :stroke-width="10" :percentage="25"></el-progress>
            </el-col>
            <el-col :span="6" style="padding: 5px;text-align: center">
              <p style="font-weight: bolder">录入量</p>
              <el-progress type="circle" :stroke-width="10" :percentage="25"></el-progress>
            </el-col>
          </el-row>
        </el-col>
        <el-col :span="12" style="padding: 0 15px">
          <p style="font-size: 14px;font-weight: bolder;padding: 0 15px">
            <span style="text-align: left">数据对比</span>
            <span style="text-align: right">查看报告</span>
          </p>
          <el-row>
            <el-col :span="18" style="padding: 5px;text-align: center">
              <div id="myChart1" style="width: 100%;height: 200px"></div>
            </el-col>
          </el-row>
        </el-col>
      </el-row>
    </div>
    <el-dialog
      :title="title"
      :visible.sync="dialogVisible"
      width="750px">
      <div id="myChart2" style="width: 100%;height: 200px"></div>
      <div>
        <h4>数据列表</h4>
        <el-table
          :data="listData"
          border
          style="width: 100%">
          <el-table-column
            type="index">
          </el-table-column>
          <el-table-column
            label="指标名称">
            <template slot-scope="scope">
              <span>{{scope.row.user_name || '--'}}</span>
            </template>
          </el-table-column>
          <el-table-column
            label="约面数">
            <template slot-scope="scope">
              <span>{{scope.row.remark || '--'}}</span>
            </template>
          </el-table-column>
          <el-table-column
            label="11月1号">
            <template slot-scope="scope">
              <span>{{scope.row.ip || '--'}}</span>
            </template>
          </el-table-column>
          <el-table-column
            label="11月2号">
            <template slot-scope="scope">
              {{scope.row.add_time * 1000 | dateFormat('YYYY/MM/DD')}}
            </template>
          </el-table-column>
          <el-table-column
            label="11月3号">
            <template slot-scope="scope">
              {{scope.row.add_time * 1000 | dateFormat('YYYY/MM/DD')}}
            </template>
          </el-table-column>
          <el-table-column
            label="11月4号">
            <template slot-scope="scope">
              {{scope.row.add_time * 1000 | dateFormat('YYYY/MM/DD')}}
            </template>
          </el-table-column>
          <el-table-column
            label="11月5号">
            <template slot-scope="scope">
              {{scope.row.add_time * 1000 | dateFormat('YYYY/MM/DD')}}
            </template>
          </el-table-column>
          <el-table-column
            label="11月6号">
            <template slot-scope="scope">
              {{scope.row.add_time * 1000 | dateFormat('YYYY/MM/DD')}}
            </template>
          </el-table-column>
          <el-table-column
            label="11月7号">
            <template slot-scope="scope">
              {{scope.row.add_time * 1000 | dateFormat('YYYY/MM/DD')}}
            </template>
          </el-table-column>
        </el-table>
      </div>
      <div slot="footer" class="dialog-footer">
        <el-button size="mini" type="danger" @click="cancle">取 消</el-button>
        <el-button size="mini" type="primary" @click="cancle">确 定</el-button>
      </div>
    </el-dialog>
  </div>
</template>
<script>
    import NavTemplent from "../../../components/NavTemplent";
    import TablePage from "../../../components/TablePage";
    export default {
        components: {
            NavTemplent,
            TablePage
        },
        data() {
            return {
                listData: [],
                departmentList: [],
                page: {
                    pageSize: 1,
                    pageNum: 10
                },
                dialogVisible: false,
                total: 0,
                title: '查看部门详情',
                form: {
                    cycle: '0',
                    date: [],
                    department_id: ''
                }
            }
        },
        methods: {
            openDetails() {
                this.dialogVisible = true;
                this.$nextTick(() => {
                    this.drawLine2()
                })
            },
            // 站点列表
            drawLine() {
                // 基于准备好的dom，初始化echarts实例
                let myChart = this.$echarts.init(document.getElementById('myChart'));
                // 绘制图表
                myChart.setOption({
                    tooltip: {
                        trigger: 'item',
                        formatter: "{a} <br/>{b}: {c} ({d}%)"
                    },
                    legend: {
                        bottom: 10,
                        orient: 'vertical',
                        x: 'right',
                        data:['搜索引擎','社交平台','直接输入','其他']
                    },
                    series: [
                        {
                            name:'访问来源',
                            type:'pie',
                            radius: ['50%', '70%'],
                            avoidLabelOverlap: false,
                            label: {
                                normal: {
                                    show: false,
                                    position: 'center'
                                },
                                emphasis: {
                                    show: true,
                                    textStyle: {
                                        fontSize: '30',
                                        fontWeight: 'bold'
                                    }
                                }
                            },
                            labelLine: {
                                normal: {
                                    show: false
                                }
                            },
                            data:[
                                {value:1548, name:'搜索引擎'},
                                {value:335, name:'社交平台'},
                                {value:310, name:'直接输入'},
                                {value:234, name:'其他'}
                            ]
                        }
                    ]
                });
            },
            drawLine1() {
                // 基于准备好的dom，初始化echarts实例
                let myChart = this.$echarts.init(document.getElementById('myChart1'));
                // 绘制图表
                myChart.setOption({
                    tooltip: {
                        trigger: 'item',
                        formatter: "{a} <br/>{b}: {c} ({d}%)"
                    },
                    legend: {
                        bottom: 10,
                        orient: 'vertical',
                        x: 'right',
                        data:['搜索引擎','社交平台','直接输入','其他']
                    },
                    series: [
                        {
                            name:'访问来源',
                            type:'pie',
                            radius: ['50%', '70%'],
                            avoidLabelOverlap: false,
                            label: {
                                normal: {
                                    show: false,
                                    position: 'center'
                                },
                                emphasis: {
                                    show: true,
                                    textStyle: {
                                        fontSize: '30',
                                        fontWeight: 'bold'
                                    }
                                }
                            },
                            labelLine: {
                                normal: {
                                    show: false
                                }
                            },
                            data:[
                                {value:1548, name:'搜索引擎'},
                                {value:335, name:'社交平台'},
                                {value:310, name:'直接输入'},
                                {value:234, name:'其他'}
                            ]
                        }
                    ]
                });
            },
            drawLine2() {
                let myChart = this.$echarts.init(document.getElementById('myChart2'));
                myChart.setOption({
                    title: {text: '各项指标数据'},
                    xAxis: {
                        type: 'category',
                        data: ['Mon', 'Tue', 'Wed', 'Thu', 'Fri', 'Sat', 'Sun']
                    },
                    yAxis: {
                        type: 'value'
                    },
                    series: [{
                        data: [574, 932, 604, 934, 290, 1130, 720],
                        type: 'line'
                    }]
                });
            },
            // 部门列表
            getDepartmentList() {
                this.$post('/getDepartmentList').then(res => {
                    this.departmentList = res.list
                }).catch(err => {
                    this.$message.error(err)
                });
            },
            cancle() {
                this.dialogVisible = false;
                this.$emit('close')
            }
        },
        mounted() {
            this.getDepartmentList();
            this.drawLine();
            this.drawLine1();
        }
    }
</script>

<style lang="scss" scoped>
  .tableTitle{
    padding: 15px;
    h5{
      font-weight: bolder;
    }
  }
  .el-select{
    width: 100%;
  }
  .tab {
    margin: 10px;
    .active{
      background: #409EFF;
      color: #fff;
    }
    span {
      font-size: 14px;
      border-radius: 5px;
      border: 1px solid #e6e3e3;
      padding: 5px 10px;
    }
  }
</style>



